.logic{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.logic form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding:2rem;
    flex-direction: column;
    width:60%;


}
